html, body, #root {
    width: 100%;
    height: 100%;
    overflow: hidden;
}

body {
    margin: 0px;
    padding: 0px;
}

#board-canvas {
    width: 100%;
    height: 100%;
    cursor: grab;
    image-rendering: pixelated;
}

#title-input-container {
    width: 100%;
    display: flex;
    justify-content: center;
}

#title-input {
    border: 0;
    outline: 0;
    width: 20%;
    height: 5%;
    text-align: center;
    color: white;
    font-size: 1.5em;
    background-color: transparent;
    transition: 0.1s;
}

#title-input:focus {
    outline: none !important;
    background-color: rgba(208, 208, 208, 0.403);
}

#title-input:hover {
    background-color: rgba(208, 208, 208, 0.403);
}